import React from 'react';

import { createStackNavigator } from '@react-navigation/stack';

import Title from '../components/Title';
import LikedIcon from '../components/LikedIcon';
import {colors} from '../constants/colors';

import ProfileScreen from '../MainApp/ProfileScreen';


const Stack = createStackNavigator();

class ProfileScreenSingleStack extends React.Component{
    render(){
        //console.log("in single screen stack for profile");
        //console.log(this.props)
        return(
            <Stack.Navigator>
                <Stack.Screen
                    name="ProfileScreen"
                    component={ProfileScreen}
                    initialParams={{...this.props.route.params}}
                    
                    options={{
                        headerTitleAlign: 'center',
                        headerStyle : {backgroundColor: colors.primary},
                        headerTitle : () => (<Title/>)
                    }}
                />
            </Stack.Navigator>
        )
    }
}


export default ProfileScreenSingleStack; // Just for providing header for this screen separately cuz hiding parent header in search screen was becoming difficult and so the back button on SearchResult screen was not visible

