import React from 'react';
import {StyleSheet, View, TouchableOpacity, Dimensions, ScrollView} from 'react-native';
import {Layout, Text} from '@ui-kitten/components';

import { createStackNavigator } from '@react-navigation/stack';
import { withNavigationFocus } from "@react-navigation/compat";

import Title from '../components/Title';
import LikedIcon from '../components/LikedIcon';
import { colors } from '../constants/colors';

import EventsScreen from '../MainApp/EventsScreen';

const WindowHeight = Dimensions.get('window').height;
const WindowWidth = Dimensions.get('window').width;





const Stack = createStackNavigator();

class EventsScreenSingleStack extends React.Component{
    render(){
        //console.log("in EventsSingleScreen stack");
        //console.log(this.props)
        return(
            <Stack.Navigator>
                <Stack.Screen
                    name="EventsScreen"
                    component={EventsScreen}
                    initialParams={{...this.props.route.params}}
                    
                    options={{
                        headerTitleAlign: 'center',
                        headerStyle : {backgroundColor: colors.primary},
                        headerTitle : () => (<Title/>) ,
                        headerRight : () => (<LikedIcon/>)
                    }}
                />
            </Stack.Navigator>
        )
    }
}

export default EventsScreenSingleStack;  // Just for providing header for this screen separately cuz hiding parent header in search screen was becoming difficult and so the back button on SearchResult screen was not visible






