import React from 'react';
import {StyleSheet, View, TouchableOpacity, TouchableWithoutFeedback, Dimensions, Image, ImageBackground, ScrollView, Share , ToastAndroid} from 'react-native';
import {Layout, Text} from '@ui-kitten/components';
import {Feather, Ionicons, FontAwesome} from '@expo/vector-icons';

import {colors} from '../constants/colors';
import LikedIcon from '../components/LikedIcon';
import * as Notifications from 'expo-notifications';
import * as Permissions from 'expo-permissions'
const WindowWidth = Dimensions.get('window').width;

Notifications.setNotificationHandler({   // to show local notifications even when the App is running in foreground
    handleNotification: async()=>{
        return { 
            shouldShowAlert:true
        }
    }
});

class Events extends React.Component{


    state={
        iconName : (this.props.route.params.initialLikedStatus)?"md-heart":"md-heart-empty",
        iconColor : (this.props.route.params.initialLikedStatus)?"red":"white",
        subscription : null,
        backgroundSubscription : null
    }

    componentDidMount(){
        this.state.subscription=Notifications.addNotificationReceivedListener((notification) => {});
        this.state.backgroundSubscription=Notifications.addNotificationResponseReceivedListener((notification) => {});
        this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }

    componentWillUnmount(){
        
        return ()=>{  this.state.subscription.remove();
                      this.state.backgroundSubscription.remove();  
        }  
    }
    like = ()=>{
        if(this.state.iconColor=="red"){
            this.setState({iconName:"md-heart-empty", iconColor:"white"})
        }
        else{
            this.setState({iconName:"md-heart", iconColor:"red"})
        }

        this.props.route.params.likeDislikeEvent(this.props.route.params.id)
    }


    onShare= async() => {
    // const uri = await getGalleryImageAsync(0); 
    
        const result=Share.share(
        {	message:"Welcome to \""+this.props.route.params.title+"\" brought to you by Follege. Register for the event : "+this.props.route.params.link,
            title:'Event',
        },{});
        if(result.action === Share.sharedAction) {
        if (result.activityType) {
            // shared with activity type of result.activityType
            Alert.alert("Share succesful");
        } else {
            // shared
            Alert.alert("Share succesful");
        }
        } else if (result.action === Share.dismissedAction) {
        // dismissed
        Alert.alert("Dismissed");
        }
    }


    openInAppBrowser = ()=>{
        this.props.navigation.navigate("InAppBrowserEvent", {link:this.props.route.params.link});
    }

    setReminderNotif = (params) =>{
        const timeInMilliSecondsFromRef=new Date(params.startDateTime);
        // console.log(timeInMilliSecondsFromRef.getTime())
        let currTime = new Date();

        if(currTime>=timeInMilliSecondsFromRef){
            alert("This event has already begun");
            return;
        }

        currTime.setHours(currTime.getHours()+1);
        //alert(timeInMilliSecondsFromRef-currTime);
        // console.log(currTime)
        const secs=(timeInMilliSecondsFromRef-currTime )/1000;
        //alert(secs)
        // console.log(secs)
        // const trigger= params.startDateTime;

        if(secs<=0){
            alert("This event is about to start in less than an hour");
            return;
        }
        
        Permissions.getAsync(Permissions.NOTIFICATIONS)
          .then( (statusObj)=> {
              if(statusObj.status!=='granted'){
                  return Permissions.askAsync(Permissions.NOTIFICATIONS);
              }
              return statusObj;
          }).then( (statusObj) => {
              if(statusObj.status!=='granted')
              {
                  return;
              }
          });


        Notifications.scheduleNotificationAsync({
            content:{ 
                title: 'Follege Event Reminder',
                body: params.title+" by " + params.by + " at " +params.startDateTime
            },
             trigger:{  seconds: secs } // by default we have to set it to secs
        });
        ToastAndroid.show("Reminder set ", ToastAndroid.SHORT);

    }

    get_12hrs_time = (d)=>{

        let startTime = "";

        let hrs = d.getHours();
        let min = d.getMinutes();
        let timeSuff = "am";
        if(hrs>=12){
            timeSuff = "pm";
            if(hrs>12){
                hrs -= 12;
            }
        }
        else{
            if(hrs==0){
                hrs = 12;
            }
        }

        startTime += hrs;

        if(min>0){
            if(min<10){
                min = "0"+min;
            }
            startTime += "."+min;
        }

        startTime += " "+timeSuff;

        return startTime;

    }

    adjustDateTimeFormat = (d)=>{

        let monthArr = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']

        let time_12hrs = this.get_12hrs_time(d);

        let date = d.getDate();
        let month = 1+d.getMonth();
        let year = d.getFullYear();

        if(date<10){
            date = "0"+date;
        }

        if(month<10){
            month = "0"+month;
        }

        let DateTime = date+" "+monthArr[month-1]+" "+year+" ("+time_12hrs+")";

        return DateTime;
    }

    render(){

        //let d = new Date("22 Aug 2020 14:00");
        //alert(d);
        //console.log("in Event :-")
        //console.log(this.props)
        //alert(this.props.route.params.initialLikedStatus)

        let params = this.props.route.params;

        const title = params.title;
        const by = params.by;
        
        const time = this.adjustDateTimeFormat(new Date(params.startDateTime)) + " - " + this.adjustDateTimeFormat(new Date(params.endDateTime)) ;
        
        const venue = params.location;
        const cost = (params.cost=="0"||params.cost=="Fr")?"Free":params.cost;
        const about = params.about;
        const imageUrl = params.imageUrl;

        return(
            <Layout style={styles.container}>
                <ScrollView>
                    <View style={styles.imageContainer}>
                        <ImageBackground
                            source={require('../assets/Image.png')}
                            style={styles.image}
                        >
                        <TouchableWithoutFeedback
                            onPress={ ()=>{this.props.navigation.navigate("EventImage", {imageUrl:imageUrl})} }
                        >
                            <ImageBackground
                                source={{uri : imageUrl}}
                                style={styles.image}
                            />
                        </TouchableWithoutFeedback>
                        </ImageBackground>
                    </View>
                    <View style={styles.btnsContainer}>
                        <TouchableOpacity
                            style={styles.likeBtn}
                            onPress={this.like}
                        >
                            <Ionicons name={this.state.iconName} color={this.state.iconColor} size={24} />
                            <Text style={styles.btnText}>LIKE</Text>
                        </TouchableOpacity>
                        <TouchableOpacity
                            style={styles.shareBtn}
                            onPress={this.onShare}
                        >
                            <Feather name="send" size={24} color="white"/>
                            <Text style={styles.btnText}>SHARE</Text>
                        </TouchableOpacity>
                    </View>

                    <View style={styles.contentContainer}>
                        <Text style={styles.title}>{title}</Text>
                        <Text style={styles.by}>{by}</Text>
                        <View style={styles.txtContainer}>
                            <Feather name="calendar" size={24} color='white'/>
                            <Text style={[styles.txt, {color:colors.brandGreen}]}>{time}</Text>
                        </View>
                        <View style={styles.txtContainer}>
                            <Feather name="map-pin" size={24} color='white'/>
                            <Text style={styles.txt}>{venue}</Text>
                        </View>
                        <View style={[styles.txtContainer, {marginHorizontal:6}]}>
                            <FontAwesome name="rupee" size={24} color='white'/>
                            <Text style={[styles.txt, {marginHorizontal:22}]}>{cost}</Text>
                        </View>

                        <Text style={styles.aboutTitle}>About</Text>
                        <Text style={styles.aboutTxt}>{about}</Text>
                    
                    </View>
                </ScrollView>
                <View style={styles.bottomBtnsContainer}>
                    <TouchableOpacity 
                        style={[styles.bottomBtn, {backgroundColor:colors.btnBlue}]}
                        onPress={this.openInAppBrowser}
                    >
                        <Feather name="check-square" size={24} color='white'/>
                        <Text style={styles.bottomBtnTxt}>REGISTER</Text>
                    </TouchableOpacity>
                    <TouchableOpacity 
                        style={[styles.bottomBtn, {backgroundColor:colors.btnGreen}]}
                        onPress={()=>{this.setReminderNotif(params)}}
                    >
                        <Feather name="clock" size={24} color='white'/>
                        <Text style={styles.bottomBtnTxt}>SET REMINDER</Text>
                    </TouchableOpacity>
                </View>
            </Layout>
        )
    }
}

export default Events;

const styles = StyleSheet.create({
    container:{
        flex:1
    },
    imageContainer:{
        width:WindowWidth,
        height:0.6*WindowWidth
    },
    image:{
        height:'100%',
        width:'100%',
        resizeMode:'stretch'
    },
    btnsContainer:{
        marginTop:8,
        marginHorizontal:16,
        alignSelf:'flex-end',
        flexDirection:'row'
    },
    likeBtn:{
        flexDirection:'row',
        alignItems:'center',
        paddingVertical:4,
        paddingHorizontal:8,
        borderRadius:4,
        backgroundColor: colors.btnGrey2,
    },
    shareBtn:{
        flexDirection:'row',
        alignItems:'center',
        paddingVertical:4,
        paddingHorizontal:8,
        borderRadius:4,
        marginLeft:8,
        backgroundColor:colors.btnBlue,
    },
    btnText:{
        paddingLeft:8,
    },
    contentContainer:{
        marginTop:24,
        marginBottom:56,
        marginHorizontal:16
    },
    title:{
        fontSize:24,
        fontWeight:'bold',
    },
    by:{
        color:colors.btnBlue,
    },
    txtContainer:{
        marginTop:16,
        flexDirection:'row',
        alignItems:'center',
    },
    txt:{
        fontSize:14,
        fontWeight:'bold',
        marginHorizontal:16,
    },
    aboutTitle:{
        marginTop:40,
        fontSize:24,
        fontWeight:'bold'
    },
    aboutTxt:{
        textAlign:"justify",
        marginTop:16,
        fontSize:14,
        lineHeight:20
    },
    bottomBtnsContainer:{
        flexDirection:'row'
    },
    bottomBtn:{
        flex:1,
        flexDirection:'row',
        justifyContent:'center',
        alignItems:'center',
        paddingVertical:16,
    },
    bottomBtnTxt:{
        paddingLeft:8,
        fontSize:14,
        fontWeight:'bold'
    }
});