import React from 'react';
import { StyleSheet, Text, View,TouchableOpacity,Image ,Modal} from 'react-native';
import { SimpleLineIcons } from '@expo/vector-icons'; 
import { Feather } from '@expo/vector-icons'; 
import Values from "../assets/Values";
import * as Notifications from 'expo-notifications';
import PrivacyPol from './PrivacyPol';
import TNC from './TNC';
import Card from './Card';
class SignUp1 extends React.Component{
  constructor(){
    super()
    this.state={
      // modalNo:0  // tells which modal to open 
      //           //   0-> modal closed 
      //           // 1-> TnC Modal
      //           // 2-> Privacy Policy modal
      //           ,
      isTnC_Vis:false, // manages openeing and closing of terms and conditions modal
      isPvP_vis:false  // manages openeing and closing of privacy policy modal
     } 
    }
    setFalse=()=>{
      this.setState({isTnC_Vis:false });
      this.setState({isPvP_vis:false });
    }
    sendNotif()
    {
      Notifications.scheduleNotificationAsync({
        content:{
          title:'Welcome to Follege Notifications',
          body:'Your College Life is now upgrading'
        },
        trigger:{
          seconds: 10, 
        }
      });
    }
    render(){
      if(this.state.isTnC_Vis) return<TNC setFalse={this.setFalse} />;
      else if(this.state.isPvP_vis) return <PrivacyPol setFalse={this.setFalse} />
      else
      //console.log(Values.Dim.W_Height)
      return ( <>
        <View style={styles.container}>
              <View style={styles.upperBox}><Image 
                                                source={require('../assets/lgn.png')}
                                                fadeDuration= {0}
                                                style={styles.image}

                                                resizeMode="contain"
                                                />
            </View>
         
            <View style={styles.buttonRow}>
                <TouchableOpacity style={styles.button2}  onPress={()=>this.props.nextPress(4)}><Text style={styles.whiteText}>Log In</Text></TouchableOpacity>
                <TouchableOpacity style={styles.button1}  onPress={()=>this.props.nextPress(5)}><Text style={styles.whiteText}>Sign Up</Text></TouchableOpacity>
            </View>
             <TouchableOpacity style={{...styles.whiteBand,marginTop:15}} onPress={()=>this.props.signInWithGoogle()}>
                <SimpleLineIcons name="social-google" size={24} color="black" />
                <Text style={{color:'black',  fontSize:18,
       fontFamily:'Montserrat' }} > Sign Up with Google</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.whiteBand} onPress={()=>this.props.signInWithFaceBook()}>
               <Feather name="facebook" size={24} color="black" />
                <Text style={{color:'black',  fontSize:18,
       fontFamily:'Montserrat' }} > Sign Up with Facebook</Text>
            </TouchableOpacity>
            {/* <TouchableOpacity style={styles.button3}  onPress={()=>this.props.nextPress(6)}><Text style={styles.whiteText}>Skip</Text></TouchableOpacity>
            <TouchableOpacity style={styles.button3}  onPress={()=>this.sendNotif()}><Text style={styles.whiteText}>Remind me about follege</Text></TouchableOpacity> */}
            <View style={styles.emptyBox}>
            <Text style={{color:'white', fontSize:12,
       fontFamily:'Montserrat'}}>By creating new account you agree to our<Text onPress={() =>  this.setState({isTnC_Vis:true})} style={{color:'#11f66f'}} > Terms and Conditions</Text> and <Text onPress={() =>  this.setState({isPvP_vis:true})} style={{color:'#11f66f'}}>Privacy Policy</Text></Text>
            </View>
        </View>
        </>
    );
    }
}

export default SignUp1;

var WindowHeight=Values.Dim.W_Height;
var WindowWidth=Values.Dim.W_Width;
const styles= StyleSheet.create({
    container:{
      flex:1,
      backgroundColor: Values.Colors.Grey,
      justifyContent:'flex-start',
      alignItems:'center',
      
    },
    whiteBand:{
        flexDirection:'row',
        justifyContent:'center',
        alignItems:'center',
        backgroundColor:'white',
        width: 0.9*Values.Dim.W_Width,
        height:50,
        margin:10
    },
    upperBox:{
        flexDirection:'row',
        justifyContent:'center',
        backgroundColor:Values.Colors.Grey,
        marginTop:0.0625*Values.Dim.W_Height,
        height:0.37*Values.Dim.W_Height,
        width:0.9*Values.Dim.W_Width
    },
    buttonRow:{
        flexDirection:'row',
        justifyContent:'center',
        alignItems:'center',
        alignContent:'center',
        width: 0.9*Values.Dim.W_Width,
        height:50,
        marginBottom:30
    },

    button1:
      {
        backgroundColor:Values.Colors.brandGreen,
        justifyContent:'center',
        alignItems:'center',
        width: 0.45*Values.Dim.W_Width,
        height:50,
        marginTop:0.058*WindowHeight,
       
    },
    button2:
      {
        backgroundColor:'#8f9bb3',
        justifyContent:'center',
        alignItems:'center',
        width: 0.45*Values.Dim.W_Width,
        height:50,
        marginTop:0.058*WindowHeight,
       
    },
    button3:
      {
        backgroundColor:'#8f9bb3',
        justifyContent:'center',
        alignItems:'center',
        width: 0.25*Values.Dim.W_Width,
        height:30,
        marginTop:0.058*WindowHeight,
       
    },
    whiteText:{
      color:'white',
      fontSize:18,
      fontFamily:'Montserrat'
   },
    emptyBox:
      {  flex:1,
        flexDirection:'column-reverse',
        alignItems:'center',
        // width: Values.Dim.W_Width,
        // backgroundColor:'red', 
        marginTop:0.058*WindowHeight,
        // paddingLeft:20,
        // paddingRight:5,
        paddingBottom:0.02*WindowHeight,
        width:0.9*Values.Dim.W_Width
    },
    image:{
      height:0.37*Values.Dim.W_Height,
        width:0.9*Values.Dim.W_Width
    }
});
  
