import React from 'react';
import {StyleSheet, View, Dimensions, TouchableOpacity, TouchableWithoutFeedback, ImageBackground, Share} from 'react-native';
import { Layout, Text } from '@ui-kitten/components';
import {Feather as Icon, Ionicons} from '@expo/vector-icons';

import {colors} from '../constants/colors';

const WindowWidth = Dimensions.get('window').width;

const month = ["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];

class Box extends React.Component{

    state={
        iconName : (this.props.initialLikedStatus)?"md-heart":"md-heart-empty",
        iconColor : (this.props.initialLikedStatus)?"red":"white"
    }


    openEvent = ()=>{
        //alert(this.state.iconColor=="red")
        this.props.navigation.navigate("Event", {...this.props.event, likeDislikeEvent:this.like, initialLikedStatus:this.getIconColor()=="red"});
    }

    getIconName = ()=>{
        if(this.props.getLikedStatusForEvent(this.props.event.id)){
            return "md-heart";
        }
        return "md-heart-empty";
    }

    getIconColor = ()=>{
        if(this.props.getLikedStatusForEvent(this.props.event.id)){
            return "red";
        }
        return "grey";
    }

    

    like = ()=>{
        if(this.getIconColor=="white"){
            this.setState({iconName:"md-heart", iconColor:"red"})
        }
        else{
            this.setState({iconName:"md-heart-empty", iconColor:"white"})
        }

        this.props.likeDislikeEvent(this.props.event.id)
    }

    formatStartTime = (d)=>{

        let startTime = "";

        let hrs = d.getHours();
        let min = d.getMinutes();
        let timeSuff = "am";
        if(hrs>=12){
            timeSuff = "pm";
            if(hrs>12){
                hrs -= 12;
            }
        }
        else{
            if(hrs==0){
                hrs = 12;
            }
        }

        startTime += hrs;

        if(min>0){
            if(min<10){
                min = "0"+min;
            }
            startTime += "."+min;
        }

        startTime += " "+timeSuff;

        return startTime;

    }

    onShare= async() => {
    // const uri = await getGalleryImageAsync(0); 
    
        const result=Share.share(
        {	message:"Welcome to \""+this.props.event.title+"\" brought to you by Follege. Register for the event : "+this.props.event.link,
            title:'Event',
        },{});
        if(result.action === Share.sharedAction) {
        if (result.activityType) {
            // shared with activity type of result.activityType
            Alert.alert("Share succesful");
        } else {
            // shared
            Alert.alert("Share succesful");
        }
        } else if (result.action === Share.dismissedAction) {
        // dismissed
        Alert.alert("Dismissed");
        }
    }


    render(){

        //console.log("in Box :-");
        //console.log(this.props.event)

        const event = this.props.event;

        const d = new Date(event.startDateTime);

        const startDate = d.getDate();
        const startMonth = month[d.getMonth()];
        const title = event.title;
        const startTime = this.formatStartTime(d);
        const by = event.by;

        return(
            <Layout style={styles.container}>
                <View style={styles.imageContainer}>
                    
                    <ImageBackground
                        source={require('../assets/Image.png')}
                        style={styles.image}
                    >
                    <TouchableWithoutFeedback
                        onPress={this.openEvent}
                    >
                    <ImageBackground
                        source={{uri : event.imageUrl}}
                        style={styles.image}
                    >
                        <View style={{flexDirection:'row', justifyContent:'space-between'}}>
                            <View style={styles.dateContainer}>
                                <Text style={{fontSize:24, lineHeight:24, fontWeight:'bold'}}>{startDate}</Text>
                                <Text style={{fontSize:12, lineHeight:12, fontWeight:'bold'}}>{startMonth}</Text>
                            </View>
                            <TouchableOpacity
                                style={styles.likeContainer}
                                onPress={this.like}
                            >
                                <Ionicons name={this.getIconName()} color={this.getIconColor()} size={32}/>
                            </TouchableOpacity>
                        </View>
                    
                    </ImageBackground>
                    </TouchableWithoutFeedback>
                    </ImageBackground>
                    
                </View>
                <View style={styles.bottom}>
                    <TouchableWithoutFeedback 
                        onPress={this.openEvent}
                    >
                        <View style={styles.descriptionContainer}>
                            <Text 
                                numberOfLines={2} 
                                ellipsizeMode='tail' 
                                style={{fontSize:18, fontWeight:'600'}}
                            >
                                {title}
                            </Text>
                            <Text
                                numberOfLines={1} 
                                ellipsizeMode='tail'
                                style={{color: colors.brandGreen, paddingVertical:8 }}
                            >
                                {startTime+" - "+by}
                            </Text>
                        </View>
                    </TouchableWithoutFeedback>
                    <TouchableOpacity 
                        style={styles.share}
                        onPress = {this.onShare}
                    >
                        <Icon 
                            name='send' size={30} color='white'
                        />
                    </TouchableOpacity>
                </View>
            </Layout>
        )
    }
}

export default Box;

const styles = StyleSheet.create({
    container:{
        width: WindowWidth,
        borderWidth:6,
        borderColor:colors.primary
    },
    imageContainer:{
        height: 0.54*WindowWidth,
        margin: 16,
        borderRadius:2,
        overflow:'hidden'
    },
    image:{
        height:'100%',
        width:'100%',
        resizeMode:'stretch',
    },
    dateContainer:{
        backgroundColor:'black',
        opacity:0.6,
        margin:8,
        padding:5,
        alignSelf:'flex-start',
        borderRadius:4,
        justifyContent:'center',
        alignItems:'center'
    },
    likeContainer:{
        margin:8,
        padding:5,
    },
    bottom:{
        flex:1,
        flexDirection:'row',
        marginHorizontal:16,
        marginBottom:16,
    },
    descriptionContainer:{
        flex:5,
        borderWidth:0,
    },
    share:{
        flex:1,
        minWidth:20,
        minHeight:20,
        borderWidth:0,
        justifyContent:'center',
        alignItems:'center'
    }
})