import React from 'react';
import {StyleSheet, View, Image, ImageBackground, Dimensions} from 'react-native';
import {Layout, Text} from '@ui-kitten/components'; 

import LikedIcon from '../components/LikedIcon';

const WindowWidth = Dimensions.get('window').width;

class EventImage extends React.Component{

    componentDidMount(){
        this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }

    render(){
        return(
            <Layout style={styles.container}>
                <View style={styles.imageContainer}>
                    <Image 
                        source={{uri : this.props.route.params.imageUrl}}
                        style={styles.image}
                    />
                </View>
            </Layout>
        )
    }
}

export default EventImage;

const styles = StyleSheet.create({
    container:{
        flex:1,
        justifyContent:'center'
    },
    imageContainer:{
        width:WindowWidth,
        height:0.6*WindowWidth,
    },
    image:{
        height:null,
        width:null,
        flex:1,
        resizeMode:'contain'
    },
})