import React from 'react';
import {StyleSheet, View, TouchableOpacity, Dimensions, ScrollView, FlatList} from 'react-native';
import {Layout, Text} from '@ui-kitten/components';
import {Feather} from '@expo/vector-icons';

import { withNavigationFocus } from "@react-navigation/compat";

import TagsList from '../components/TagsList';
import Box from '../components/Box';
import LikedIcon from '../components/LikedIcon';
import { colors } from '../constants/colors';

const WindowHeight = Dimensions.get('window').height;
const WindowWidth = Dimensions.get('window').width;

class EventsScreen extends React.Component{


    state={
        interestsSet : new Set()
    }

    componentDidMount(){
        this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }

    addInterestFilter = (id)=>{
        //alert("selected interest ID : "+id);
    
        let set = this.state.interestsSet;
        let newSet = new Set();
        if(!set.has(id)){
            newSet.add(id);
        }
        this.flatListRef.scrollToOffset({ animated: false, offset: 0 });

        this.setState({interestsSet:newSet});
    }

    renderEvents = ({item})=>{

        //alert("Events Screen called again")

        if(item.endOfEvents){
            return(
                <View style={{marginBottom:32, alignItems:'center'}}>
                    <Text>No new event</Text>
                </View>
            )
        }

        let listEvents = this.props.route.params.listEvents;
        const likeDislikeEvent = this.props.route.params.likeDislikeEvent;
        const likedEventsSet = this.props.route.params.likedEventsSet;
        const getLikedStatusForEvent = this.props.route.params.getLikedStatusForEvent;
        const defaultInterestsSet = this.props.route.params.getDefaultInterestsSet();

        let set = defaultInterestsSet;

        //alert(JSON.stringify(listEvents))

        if(this.state.interestsSet.size>0){
            set = this.state.interestsSet;
        }

        let today = new Date();

                    let endDateTime = new Date(item.endDateTime);

                    if(endDateTime<today){
                        return <View />
                    }

                    let flag = false;

                    let arr = item.interests;

                    for(let i=0; i<arr.length; i++){
                        if(set.has(arr[i].id)){
                            flag = true;
                            break;
                        }
                    }

                    if(flag==false){
                        return <View/>
                    }

                    return(
                        <Box 
                            navigation={this.props.navigation}
                            event={item}
                            likeDislikeEvent={likeDislikeEvent}
                            initialLikedStatus={getLikedStatusForEvent(item.id)}
                            getLikedStatusForEvent={getLikedStatusForEvent}
                        />
                    )  

    }

    render(){

        //console.log("in EventsScreen :-")
        //console.log(this.props)

        let params = this.props.route.params;

        let dataForFlatList = [...params.listEvents];//params.listEvents.length ? new Array(params.listEvents) : [];
        dataForFlatList.push({endOfEvents:true, id:"end"});

        return(
            <>
            <TouchableOpacity
                onPress={()=>{this.flatListRef.scrollToOffset({ animated: true, offset: 0 })}}
                style={styles.floatingBtn}
            >
                <Feather name="chevron-up" size={32} color={colors.brandBlue}/>
            </TouchableOpacity>
            
                <Layout style={styles.container}>
                    <View>
                        <TagsList 
                            listInterests={this.props.route.params.listInterests}
                            interestsSet={this.state.interestsSet} 
                            addInterestFilter={this.addInterestFilter}
                        />
                    </View>
                    
                    <View style={{flex:1}}>
                        <FlatList
                            ref={(ref) => { this.flatListRef = ref; }}
                            data={dataForFlatList}
                            extraData={this.state.interestsSet}
                            renderItem={this.renderEvents}
                            keyExtractor={item => JSON.stringify(item.id)}
                        />
                    </View>  
                    
                    
                </Layout>
            
            </>
        )
    }

}

export default withNavigationFocus(EventsScreen);


const styles = StyleSheet.create({
    container:{
        flex:1,
        minHeight:WindowHeight-128,
    },
    floatingBtn:{
        position:"absolute",
        bottom:16,
        right:16,
        height:48,
        width:48,
        borderRadius:24,
        backgroundColor:"white",
        zIndex:1,

        justifyContent:'center',
        alignItems:'center',

        shadowColor: '#000',
        shadowOffset: { width: 0, height: 1 },
        shadowOpacity: 0.8,
        shadowRadius: 2,  
        elevation: 8,
    }
});
