import React from 'react';
import {StyleSheet, Dimensions, View, Text, TouchableOpacity, ScrollView, ColorPropType} from 'react-native';

import {colors} from '../constants/colors';
import LikedIcon from '../components/LikedIcon';
import InterestList from '../components/InterestList';
import TagForSignUp from '../components/TagForSignUp';

const WindowHeight = Dimensions.get('window').height;
const WindowWidth = Dimensions.get('window').width;

//const arr = InterestList();

class EditInterests extends React.Component{


  constructor(props){
      super(props);
      this.state={
          interestsSet : props.route.params.getDefaultInterestsSet()
      }
  }

    componentDidMount(){
            this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }

  addRemoveInterest = (id)=>{
    let currSet = this.state.interestsSet;
    let newSet = new Set(currSet);

    if(currSet.has(id)){
      newSet.delete(id);
    }
    else{
      newSet.add(id);
    }

    this.setState({interestsSet:newSet});

  }

  done = ()=>{

    if(this.state.interestsSet.size<3){
      alert("Please select at least 3 interests");
      return;
    }


    // API call for saving the initial interests while sign Up

    this.props.route.params.changeDefaultInterests(this.state.interestsSet);

    alert("Interests changed successfully");

    this.props.navigation.pop();
  }

  renderInterestTags = ()=>{

    let arr = this.props.route.params.listInterests;

    return(
      arr.map(
        (item, idx)=>{
          return(
            <View key={idx} style={styles.tagContainer}>
              <TagForSignUp
                tag = {item}
                isSelected = {this.state.interestsSet.has(item.id)}
                addRemoveInterest = {this.addRemoveInterest}
              />
            </View>
          )
        }
      )
    )

  }

  render(){

    const {fullName} = this.props.route.params;

    return(
      <View style={styles.container}>
        <ScrollView>
          <Text style={styles.introTxt}>Hey <Text style={{...styles.introTxt, fontWeight:'bold'}}>{fullName}</Text>, What are you Interested in?</Text>
          <Text style={styles.introTxt2}>Select at least 3 :</Text>
          <View style={styles.allTagsContainer}>
            {
              this.renderInterestTags()
            }
          </View>
        </ScrollView>
        <TouchableOpacity
          style={styles.btnContinue}
          onPress={this.done}
        >
          <Text style={styles.btnTxt}>
            Continue
          </Text>
        </TouchableOpacity>
      </View>
    )
  }

}

export default EditInterests;

const styles = StyleSheet.create({
  container:{
    flex:1,
    backgroundColor: colors.brandBlue
  },
  introTxt:{
    color:'white',
    fontSize: 24,
    marginTop: 32,
    marginHorizontal: 16,
  },
  introTxt2:{
    color:'white',
    marginTop: 40,
    marginHorizontal:16+8,
    fontWeight:'bold',
    fontSize:14,
  },
  allTagsContainer:{
    flexDirection:'row',
    flexWrap: 'wrap',
    justifyContent: 'flex-start',
    margin: 16,
  },
  tagContainer:{
    margin: 8
  },
  btnContinue:{
    backgroundColor: colors.brandGreen,
    minHeight: 56,
    padding: 8,
    justifyContent:'center',
    alignItems:'center',
  },
  btnTxt:{
    fontSize: 24,
    color: 'white',
    fontWeight:'bold'
  }
})
