import React from 'react';
import {StyleSheet, View, ScrollView, TouchableOpacity} from 'react-native';
import {Layout, Text} from '@ui-kitten/components';
import {Feather} from '@expo/vector-icons';

import { withNavigationFocus } from "@react-navigation/compat";

import {colors} from '../constants/colors';
import Box from '../components/Box';
import LikedIcon from '../components/LikedIcon';

class SearchResult extends React.Component{


    componentDidMount(){
        this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }


    renderEvents = ()=>{

        let { resultListEvents, getLikedStatusForEvent, likeDislikeEvent } = this.props.route.params;
        //alert("Re rendering search result : "+getLikedStatusForEvent(1))

        resultListEvents.sort(
            (a,b)=>{
                let d1 = new Date(a.startDateTime);
                let d2 = new Date(b.startDateTime);
                return d1-d2;
            }
        );
        
        return(

            resultListEvents.map(
                (item, idx)=>{
                    return(
                        <Box
                            key={idx}
                            navigation={this.props.navigation}
                            event={item}
                            likeDislikeEvent={likeDislikeEvent}
                            initialLikedStatus={getLikedStatusForEvent(item.id)}
                            getLikedStatusForEvent={getLikedStatusForEvent}
                        />
                    )
                }
            )

        )

    }


    render(){

        //console.log("in SearchResult");
        //console.log(this.props.route.params.resultListEvents);

        const { resultListEvents, selectedDay } = this.props.route.params;

        return(
            <Layout style={styles.container}>
                <TouchableOpacity
                    onPress={()=>{this.scrollCustom.scrollTo({x:0, y:0, animated:true})}}
                    style={styles.floatingBtn}
                >
                    <Feather name="chevron-up" size={32} color={colors.brandBlue}/>
                </TouchableOpacity>
                <ScrollView ref={(ref)=>{this.scrollCustom=ref}}>
                    <Text style={styles.introText}>{resultListEvents.length} search results found for : <Text style={{...styles.introText, color:colors.brandGreen}}>{selectedDay}</Text></Text>
                    {
                        this.renderEvents()
                    }
                </ScrollView>
            </Layout>
        )
    }

}

export default withNavigationFocus(SearchResult); 

const styles = StyleSheet.create({
    container:{
        flex:1,
    },
    introText:{
        margin:16,
        fontSize:18,
        fontWeight:"600"
    },
    floatingBtn:{
        position:"absolute",
        bottom:16,
        right:16,
        height:48,
        width:48,
        borderRadius:24,
        backgroundColor:"white",
        zIndex:1,

        justifyContent:'center',
        alignItems:'center',

        shadowColor: '#000',
        shadowOffset: { width: 0, height: 1 },
        shadowOpacity: 0.8,
        shadowRadius: 2,  
        elevation: 8,
    }
})