import React from 'react';
import {StyleSheet} from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { createStackNavigator } from '@react-navigation/stack';
import { BottomNavigation, BottomNavigationTab, Layout, Text, Icon } from '@ui-kitten/components';

import SearchStack from './SearchStack';
import EventsScreenSingleStack from './EventsScreenSingleStack';
import ProfileScreenSingleStack from './ProfileScreenSingleStack';
import colors from '../constants/colors'

const { Navigator, Screen } = createBottomTabNavigator();

const PersonIcon = (props) => (
  <Icon {...props} name='search-outline'/>
);

const BellIcon = (props) => (
  <Icon {...props} name='calendar-outline'/>
);

const EmailIcon = (props) => (
  <Icon {...props} name='person-outline'/>
);

const useBottomNavigationState = (initialState = 0) => {
  const [selectedIndex, setSelectedIndex] = React.useState(initialState);
  return { selectedIndex, onSelect: setSelectedIndex };
};

export const BottomTabBar = ({navigation, state}) => {

  const bottomState = useBottomNavigationState();

  return (
    <React.Fragment>

      <BottomNavigation 
        style={styles.bottomNavigation} {...bottomState}
        selectedIndex={state.index}
        onSelect={index => {navigation.navigate(state.routeNames[index]); } }
      >
        <BottomNavigationTab icon={PersonIcon}/>
        <BottomNavigationTab icon={BellIcon}/>
        <BottomNavigationTab icon={EmailIcon}/>
      </BottomNavigation>

    </React.Fragment>
  );
};

export default (props) => {
  
  //console.log("in Layer1 xcv: ");
  
  //console.log(props.route

  //console.log("Layer1 called again" )

  return(
      <Navigator initialRouteName="EventsScreenSingleStack" tabBar={props => <BottomTabBar {...props} />}>
        <Screen name='SearchStack' component={SearchStack} initialParams={{...props.route.params}}/>
        <Screen name='EventsScreenSingleStack' component={EventsScreenSingleStack} initialParams={{...props.route.params}}/>
        <Screen name='ProfileScreenSingleStack' component={ProfileScreenSingleStack} initialParams={{...props.route.params}}/>
      </Navigator>
  );

}
                                                   
                                                      
const styles = StyleSheet.create({
  bottomNavigation: {
    marginVertical: 0,
  },
});