import React from 'react';
import {View, Text, StyleSheet, ScrollView} from 'react-native';

import LikedIcon from '../components/LikedIcon';

const content = `
We all know that college is the time for networking and exploration, but despite that, the harsh reality is we don't even know about our classmates, let alone the other college students.

Moreover , in a country like India where education infrastructure is not strong, there is a huge gap between the students and the administration.

And these big social platforms like FB-Instagram, they are nothing but a sea of information, due to which 60-70% of important information is often missed out!
`;

const endingLine = `College as an environment needs a better platform and we at a Follege aim to provide a sense of belongingness to every student.`;



class AboutUsPage extends React.Component{

    componentDidMount(){
        this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }

    render(){
        return(
            <ScrollView>
                <View style={styles.container}>
                    <Text style={styles.title}>Why Follege ?</Text>
                    <Text style={{fontSize:20}}>{content}</Text>
                    <Text style={{fontSize:20, fontWeight:'bold'}}>{endingLine}</Text>
                </View>
            </ScrollView>
        )
    }
}

export default AboutUsPage;

const styles = StyleSheet.create({
    container:{
        flex:1,
        margin:16
    },
    title:{
        fontSize:30,
        fontWeight:'bold',
        justifyContent:'center',
        alignItems:'center',
        alignSelf:'center'
    },

})