import React from 'react';
import {StyleSheet, View, TouchableOpacity, ScrollView} from 'react-native';
import {Layout, Text} from '@ui-kitten/components';

import LikedIcon from '../components/LikedIcon';
import DayRow from '../components/DayRow';
import { colors } from '../constants/colors';

const arr = ["Today", "Tomorrow", "This Week", "This Weekend", "Next Week"];

class SearchScreen extends React.Component{

    state={
        selectedId:0,
    }

    componentDidMount(){
        this.props.navigation.setOptions({
            headerRight : ()=>(<LikedIcon navigation={this.props.navigation}/>)
        })
    }

    selectId = (id)=>{
        this.setState({selectedId:id});
    }

    renderDays = () => (
        arr.map(
            (item, idx)=>(
                <DayRow
                    key={idx+1}
                    curr={this.state.selectedId}
                    id={idx+1}
                    day={item}
                    selectId={this.selectId}
                />
            )
        )
    )

    search = ()=>{

        if(this.state.selectedId==0){
            alert("Please Select an option first");
            return;
        }


        let minDate=new Date(), maxDate=new Date();

    	if(this.state.selectedId==1){  // today

    	}else if(this.state.selectedId==2){  // tomorrow

    		minDate.setDate(minDate.getDate()+1);
    		maxDate.setDate(maxDate.getDate()+1);

    	}else if(this.state.selectedId==3){ // This Week

    		let x = (minDate.getDay()+6)%7;  //getDay() returns 0 for sunday , 1 for mon, ...

    		let days = 6-x;

    		maxDate.setDate(maxDate.getDate()+days);

    	}else if(this.state.selectedId==4){  // This Weekend

    		let x = (minDate.getDay()+6)%7;

    		if(x<6){
    			let days = 5-x;
    			minDate.setDate(minDate.getDate()+days);
    			maxDate.setDate(maxDate.getDate()+days+1);
    		}

        }
        else if(this.state.selectedId==5){  // Next Week

            let x = (minDate.getDay()+6)%7;

            let days = 6-x;

            minDate.setDate(minDate.getDate()+days+1);
            maxDate.setDate(maxDate.getDate()+days+1+7);
        }

        minDate.setHours(0,0,0,0);
        maxDate.setHours(23,59,59,0);
        

        let listEvents = this.props.route.params.listEvents;

        let newList = [];

        //console.log("filtering started :-")
        //console.log(minDate.getDate());
        //console.log(maxDate.getDate());

        listEvents.map(
            (item, idx)=>{
                let st1 = minDate;
                let e1 = maxDate;
                let st2 = new Date(item.startDateTime);
                let e2 = new Date(item.endDateTime);

                let a = (st1>st2)?st1:st2; // max(st1, st2)
                let b = (e1<e2)?e1:e2; //min(e1, e2);
                /*
                if(a<=b){
                    newList.push(item);
                    //console.log(item.id);
                }
                */
                if(st2>=st1 && st2<=e1){
                    newList.push(item);
                }
            }
        )

        this.props.navigation.navigate("SearchResult", {selectedDay:arr[this.state.selectedId-1] , resultListEvents:newList});
    }

    render(){

        //console.log("in SearchScreen");
        //console.log(this.props);

        return(
            <Layout style={styles.container}>
                <ScrollView>
                    <View style={styles.quesContainer}>
                        <Text style={styles.ques}>When do you want to attend an event?</Text>
                    </View>
                    <View style={styles.daysContainer}>
                        {
                            this.renderDays()
                        }
                    </View>
                    <TouchableOpacity
                        style={styles.btn}
                        onPress={this.search}
                    >
                        <Text style={styles.btnText}>SEARCH</Text>
                    </TouchableOpacity>
                </ScrollView>
            </Layout>
        )
    }

}

export default SearchScreen;

const styles = StyleSheet.create({
    container:{
        flex:1
    },
    quesContainer:{
        marginTop:64,
        marginHorizontal:16,
    },
    ques:{
        fontSize:18
    },
    daysContainer:{
        marginTop:40,
    },
    btn:{
        alignSelf:'center',
        marginTop:24,
        marginHorizontal:16,
        paddingHorizontal:24,
        paddingVertical:8,
        borderRadius:4,
        backgroundColor:colors.brandGreen,
    },
    btnText:{
        fontSize:14,
        fontWeight:'bold'
    }
});