import * as React from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';

import Layer1 from './Layer1';
import Event from '../MainApp/Event';
import InAppBrowserEvent from '../MainApp/InAppBrowserEvent';
import LikedEventsScreen from '../MainApp/LikedEventsScreen';
import EditInterests from '../MainApp/EditInterests';
import TnCpage from '../MainApp/TnCpage';
import PrivacyPolicy from '../MainApp/PrivacyPolicy';
import AboutUsPage from '../MainApp/AboutUsPage';
import EventImage from '../MainApp/EventImage';
import Title from './Title';
import LikedIcon from './LikedIcon';
import { colors } from '../constants/colors';


const Stack = createStackNavigator();

class App extends React.Component{

  render(){

    //console.log("in AppNavigator :-")
    //console.log()

    return (
      <NavigationContainer>
        <Stack.Navigator
          screenOptions={{
              headerTitleAlign: 'center',
              headerTintColor:'white',
              headerBackTitle:"Back"
          }}
        >
          <Stack.Screen 
            name="Layer1" 
            component={Layer1} 
            initialParams={{...this.props}}
            options={{
              headerShown : false,
            }}
          />
          <Stack.Screen
            name="Event"
            component={Event}
            
            options={(navigation, route)=>({
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>) ,
            })}
          />
          <Stack.Screen
            name="InAppBrowserEvent"
            component={InAppBrowserEvent}

            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>) ,
            }}
          />
          <Stack.Screen
            name="LikedEventsScreen"
            component={LikedEventsScreen}
            initialParams={{...this.props}}
            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>) 
            }}
          />
          <Stack.Screen
            name="EditInterests"
            component={EditInterests}
            initialParams={{
              fullName:this.props.fullName, 
              listInterests:this.props.listInterests,
              getDefaultInterestsSet:this.props.getDefaultInterestsSet, 
              changeDefaultInterests:this.props.changeDefaultInterests
            }}
            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>) 
            }}
          />
          <Stack.Screen
            name="TnCpage"
            component={TnCpage}
            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>)
            }}
          />
          <Stack.Screen
            name="PrivacyPolicy"
            component={PrivacyPolicy}
            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>)
            }}
          />
          <Stack.Screen
            name="AboutUsPage"
            component={AboutUsPage}
            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>)
            }}
          />
          <Stack.Screen
            name="EventImage"
            component={EventImage}
            options={{
              headerStyle : {backgroundColor: colors.primary},
              headerTitle : () => (<Title/>)
            }}
          />
        </Stack.Navigator>
      </NavigationContainer>
    );
  }
}

export default App;