import React from 'react';
import {View, Text, TouchableOpacity, StyleSheet, BackHandler} from 'react-native';
import {Feather as Icon} from '@expo/vector-icons';


class Tag extends React.Component{


    select = ()=>{
        this.props.addInterestFilter(this.props.tag.id);
    }

    render(){

        const {col, icon, interest} = this.props.tag;
        const fontSize = (this.props.isSelected)?20:14 ;
        const fontBoldness = (this.props.isSelected)?"bold":"normal" ;
        const iconSize = (this.props.isSelected)?24:20 ;
        const borderWidth = (this.props.isSelected)?2:0;
        const borderColor = "white";

        return(
            <TouchableOpacity
                onPress = {this.select}
            >
                <View
                     style={[styles.container, {backgroundColor:col, borderWidth:borderWidth, borderColor:borderColor}]}
                >
                    <Icon name={icon} size={iconSize} color="white"/>
                    <Text style={[styles.interest, {fontSize:fontSize, fontWeight:fontBoldness }]}>{interest}</Text>
                </View>
            </TouchableOpacity>
        )
    }
}

export default Tag;

const styles = StyleSheet.create({
    container:{
        flexDirection:'row', 
        alignItems:'center',
        minHeight: 24,
        padding: 5,
        borderRadius:2,
    },
    interest:{
        paddingLeft:5,
        color:'white',
    }
})