import React from 'react';
import {StyleSheet, View, Text, TouchableOpacity} from 'react-native';
import {Feather as Icon} from '@expo/vector-icons';

import {colors} from '../constants/colors';

export default (props)=>{

    const col = (props.id==props.curr)?(colors.brandGreen):('white');

    return(
        <TouchableOpacity 
            style={styles.container}
            onPress={()=>{props.selectId(props.id)}}
        >
            <Text style={{...styles.text, color:col}}>{props.day}</Text>
            {
                (props.id==props.curr)?(<Icon name='check' size={24} color={col} />):(<View/>)
            }
        </TouchableOpacity>
    )
}

const styles = StyleSheet.create({
    container:{
        marginHorizontal:16,
        marginBottom:16,
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center'
    },
    text:{
        fontSize:24
    }
});