import React from 'react';
import {View, Text, ScrollView, StyleSheet} from 'react-native';

import Tag from './Tag';
import InterestList from './InterestList';


//const arr = InterestList();

class TagList extends React.Component{

    renderTags = ()=>(
        this.props.listInterests.map(
            (item, idx)=>{
                return(
                    <View key={idx} style={{padding:5}}>
                        <Tag 
                            tag={item} 
                            isSelected={this.props.interestsSet.has(item.id)}
                            addInterestFilter={this.props.addInterestFilter}
                        />
                    </View>
                )
            }
        )
    )

    render(){
        return(
            <ScrollView horizontal>
                <View style={styles.row}>
                {
                    this.renderTags()
                }
                </View>
                <View style={styles.col}>

                </View>
            </ScrollView>
        )
    }

}

export default TagList;

const styles = StyleSheet.create({
    row:{
        flexDirection:'row',
        flexWrap:'wrap',
        justifyContent: 'space-between',
        alignItems:'center',
        marginHorizontal: 16
    },
    col:{
        marginTop:16
    }
})