import React from 'react';
import {StyleSheet, Dimensions, View, Text, TouchableOpacity, ScrollView, Modal, ActivityIndicator} from 'react-native';

import {colors} from '../constants/colors';
import InterestList from '../components/InterestList';
import TagForSignUp from '../components/TagForSignUp';

import axios from 'axios';

const WindowHeight = Dimensions.get('window').height;
const WindowWidth = Dimensions.get('window').width;

//const arr = InterestList();

const url = "https://event-follege.herokuapp.com";

class Interest extends React.Component{

  state={
    listInterests: [],
    interestsSet : new Set()
  }

  listInterestsGetRequest = ()=>{

    axios.get(url+"/events/interests")
    .then(
      (response)=>{
        console.log("Response of /events/interests get request in Interest.js : ")
        console.log(response)
        this.setState({listInterests:response.data})
      }
    )
    .catch(
      (error)=>{
        alert("Error while loading Interests")
        console.log("Error while /events/interests get request in Interest.js : ")
        console.log(error)
      }
    )

  }

  componentDidMount(){
    this.listInterestsGetRequest();
  }

  addRemoveInterest = (id)=>{
    let currSet = this.state.interestsSet;
    let newSet = new Set(currSet);

    if(currSet.has(id)){
      newSet.delete(id);
    }
    else{
      newSet.add(id);
    }

    this.setState({interestsSet:newSet});

  }

  done = ()=>{

    if(this.state.interestsSet.size<3){
      alert("Please select at least 3 interests");
      return;
    }


    // API call for saving the initial interests while sign Up

    this.props.updateInterests(Array.from(this.state.interestsSet));
  }

  renderInterestTags = ()=>{

    return(
      this.state.listInterests.map(
        (item, idx)=>{
          return(
            <View key={idx} style={styles.tagContainer}>
              <TagForSignUp
                tag = {item}
                isSelected = {this.state.interestsSet.has(item.id)}
                addRemoveInterest = {this.addRemoveInterest}
              />
            </View>
          )
        }
      )
    )

  }

  render(){
    return(
      <View style={styles.container}>
        <ScrollView>
          <Text style={styles.introTxt}>Hey <Text style={{...styles.introTxt,fontWeight:'bold'}}>{this.props.fullName}</Text>, What are you Interested in?</Text>
          <Text style={styles.introTxt2}>Select at least 3 :</Text>

          <Modal
            animationType="fade"
            transparent={true}
            visible={this.state.listInterests.length==0}
            onRequestClose={() => {
              
            }}
          >
            <View style={{flex:1, justifyContent:'center', alignItems:'center'}}>
              <ActivityIndicator size="large"/>
            </View>
          </Modal>

          <View style={styles.allTagsContainer}>
            {
              this.renderInterestTags()
            }
          </View>
        </ScrollView>
        <TouchableOpacity
          style={styles.btnContinue}
          onPress={this.done}
        >
          <Text style={styles.btnTxt}>
            Continue
          </Text>
        </TouchableOpacity>
      </View>
    )
  }

}

export default Interest;

const styles = StyleSheet.create({
  container:{
    flex:1,
    backgroundColor: colors.brandBlue
  },
  introTxt:{
    color:'white',
    fontSize: 24,
    
    marginTop: 0.35*WindowWidth,
    marginHorizontal: 16,
  },
  introTxt2:{
    color:'white',
    marginTop: 40,
    marginHorizontal:16+8,
    fontSize:14,
  },
  allTagsContainer:{
    flexDirection:'row',
    flexWrap: 'wrap',
    justifyContent: 'flex-start',
    margin: 16,
  },
  tagContainer:{
    margin: 8
  },
  btnContinue:{
    backgroundColor: colors.brandGreen,
    minHeight: 56,
    padding: 8,
    justifyContent:'center',
    alignItems:'center',
  },
  btnTxt:{
    fontSize: 24,
    color: 'white',
   
  }
})

