import React from 'react';
import {View, Text, Modal, ActivityIndicator} from 'react-native';
import axios from 'axios';

import Page1 from './Page1'    // 1
import Page2 from './Page2'    // 2
import Page3 from './Page3'    // 3
import Login1 from './Login1'  // 4
import SignUp1 from './SignUp1'// 5
import Mobile from './Mobile'  // 6
import College from './College'// 7
import Interest from './Interest'//8
import * as Google from 'expo-google-app-auth';
import * as AppAuth from 'expo-app-auth';
import * as Facebook from 'expo-facebook'; 
const IOS_CLIENT_ID ='266645163533-lpcv44lmdq38ja8fppftvdiav6koo16h.apps.googleusercontent.com';
const ANDROID_CLIENT_ID= '266645163533-86cavvdgm28q24lnj6she9vmta52husr.apps.googleusercontent.com';
//`${AppAuth.OAuthRedirect}:/oauth2redirect/google`
const url = "https://follege.herokuapp.com";


class Intro extends React.Component{
  constructor(){
    super()
    this.state={
      id:1, // always set as 1
      mobNo:"",
      college:"",
      interests:[],

      isModalVisible:false,
      createAccountRes:false,
      hasAccountRes:false,
      email:"",
      fullName:"",
      username:"",
      password:"",
    
      token:"",
      refreshToken:""
    }
    this.nextPress=this.nextPress.bind(this)
    this.updateMobile=this.updateMobile.bind(this)
    this.updateCollege=this.updateCollege.bind(this)
    this.updateInterests=this.updateInterests.bind(this)
    
  }

  hasAccount = (email, from)=>{

    let data={
      "username":email
    };

    axios.post(
      url+"/users/ifloggedin",
      data
    ).then(

      (response)=>{
        console.log("response from ifloggedin API call in hasAccount() in Intro.js : "+JSON.stringify(response))
  
        this.setState({isModalVisible:false});

        if(response.data.Item){

          const {username, fullName, likedEvents, interests} = response.data.Item;
          const email = username;
          this.props.toggleLogin(true, email, fullName, interests, likedEvents);
        }
        else{
          /* // Commenting out this if else block to make login , signup equivalent
          if(from=="login"){
            alert("You don't have an account, please sign up first");
          }
          else{
            this.setState({id:6});
          }
          */
         this.setState({id:6});
        }

      }

    ).catch(

      (error)=>{
        alert("Some error occured")
        console.log("error in hasAccount() in Intro.js : "+error)
      }

    )

  }

  createAccount = (interests)=>{

    let data={
      "username":this.state.email,
      "fullName":this.state.fullName,
      "interests":interests,
      "college":this.state.college,
      "phone":this.state.mobNo
    };

    axios.post(
      url+"/users/signin",
      data
    ).then(

      (response)=>{
        console.log("response from signin API call in createAccount() in Intro.js : "+JSON.stringify(response))
        
        this.setState({isModalVisible:false});

        this.props.toggleLogin(true, this.state.email, this.state.fullName, interests, []);

      }

    ).catch(

      (error)=>{
        alert("Some error occured in creating account");
        console.log("error in hasAccount() in Intro.js : "+error)
      }

    )

  }

  signInWithGoogle  = async(from) => {
    try {
      this.setState({isModalVisible:true});
      const result = await Google.logInAsync({
        iosClientId:IOS_CLIENT_ID,
        androidClientId:ANDROID_CLIENT_ID,
        iosStandaloneAppClientId: `266645163533-09vr73p3pk344nkao7roa5r55a290n9r.apps.googleusercontent.com`,
        androidStandaloneAppClientId: "266645163533-eqamm28lc5mvu7r9tvudpv1bg5pjb3km.apps.googleusercontent.com",
        scopes:["profile","email"],
        redirectUrl: `${AppAuth.OAuthRedirect}:/oauthredirect`
      });

      if(result.type === 'success'){
        console.log(result);
        this.setState({email:result.user.email, fullName:result.user.name});

        // this.setState({isModalVisible:true});

        this.hasAccount(result.user.email, from);
        

        return result.accessToken;
      }
      else { this.setState({isModalVisible:false});
        return { cancelled: true}
      }
    } catch (error)
    {
      this.setState({isModalVisible:false});
      console.log(error)
      return {error:true};
    }
  }

  signInWithFaceBook= async (from)=> {
    try {
      this.setState({isModalVisible:true});
      await Facebook.initializeAsync('2469154320049192');
      // const {
      //   type,
      //   token,
      //   expires,
      //   permissions,
      //   declinedPermissions,
      // }
       const result= await Facebook.logInWithReadPermissionsAsync({
        permissions: ['public_profile'],
      });
      console.log(result);
      if (result.type === 'success') {
        // Get the user's name using Facebook's Graph API
        const response = await fetch(`https://graph.facebook.com/me?access_token=${result.token}`);
        const res=await response.json();
        console.log("Hi logged in:"+JSON.stringify(res)+ "checlasdfkljoij"+ res.id);
        this.setState({email:res.id, fullName:res.name}); // I am getting only name as response therefore only using name as email id

        

        this.hasAccount(res.id, from);
        

        return result.token;
      } else {
        // type === 'cancel'
        console.log("Error from else, Facebook Login Error:");
        this.setState({isModalVisible:false});
        return { cancelled: true}
      }
    } catch ({ message }) {
      console.log("Error from catch, Facebook Login Error:"+ message);
      //alert(`Facebook Login Error: ${message}`);
      this.setState({isModalVisible:false});
      return {error:true};
    }
  }
  
  nextPress(recID){
    if(recID ==9){
    
      this.setState({isModalVisible:true});
      this.createAccount(this.state.interests);

    }
     console.log("inside NextPress "+ recID)
    this.setState({id:recID})
    
  }
  updateMobile(mobnos)
  {
    this.setState({id:7,mobNo:mobnos}) 
    
  }
  updateCollege(cllg)
  {
    this.setState({id:8, college:cllg}); 
  }
  updateInterests(interests)
  {
    this.setState({isModalVisible:true, interests:interests});
    this.createAccount(interests);
  }
    render(){
      if(this.state.id==1) 
         return ( <Page1 nextPress={this.nextPress}/> );
      else if(this.state.id==2) 
               return ( <Page2 nextPress={this.nextPress}/> );
               else if(this.state.id==3) 
                        return ( <Page3 nextPress={this.nextPress}/> );
                         else if(this.state.id==4)
                            return ( 
                              <>
                              <Modal
                                animationType="fade"
                                transparent={true}
                                visible={this.state.isModalVisible}
                                onRequestClose={() => {
                                  
                                }}
                              >
                                <View style={{flex:1, justifyContent:'center', alignItems:'center'}}>
                                  <ActivityIndicator size="large"/>
                                </View>
                              </Modal>
                              <Login1 nextPress={this.nextPress}  signInWithGoogle={this.signInWithGoogle}  signInWithFaceBook={this.signInWithFaceBook}/> 
                              </>
                            );
                                else if(this.state.id==5)
                                  return ( 
                                    <>
                                    <Modal
                                      animationType="fade"
                                      transparent={true}
                                      visible={this.state.isModalVisible}
                                      onRequestClose={() => {
                                        
                                      }}
                                    >
                                      <View style={{flex:1, justifyContent:'center', alignItems:'center'}}>
                                        <ActivityIndicator size="large"/>
                                      </View>
                                    </Modal>
                                    <SignUp1 nextPress={this.nextPress} signInWithGoogle={this.signInWithGoogle} signInWithFaceBook={this.signInWithFaceBook} /> 
                                    </>
                                  );
                                    else if(this.state.id==6)
                                    return ( <Mobile nextPress={this.nextPress} updateMobile={this.updateMobile}/> );
                                        else if(this.state.id==7)
                                         return ( <College nextPress={this.nextPress} updateCollege={this.updateCollege}/> );
                                            else if(this.state.id==8)
                                                return ( 
                                                  <>
                                                  <Modal
                                                    animationType="fade"
                                                    transparent={true}
                                                    visible={this.state.isModalVisible}
                                                    onRequestClose={() => {
                                                      
                                                    }}
                                                  >
                                                    <View style={{flex:1, justifyContent:'center', alignItems:'center'}}>
                                                      <ActivityIndicator size="large"/>
                                                    </View>
                                                  </Modal>
                                                  <Interest 
                                                    fullName={this.state.fullName}
                                                    nextPress={this.nextPress} 
                                                    updateInterests={this.updateInterests}
                                                  /> 
                                                  </>
                                                );
                                              return <View></View>;

    }
}

export default Intro;

