import React from 'react';
import {TouchableOpacity} from 'react-native';
import {AntDesign as Icon} from '@expo/vector-icons';

export default (props) => {

    //console.log("in Likedicon :-")
    //console.log(props)

    return(
        <TouchableOpacity
            style={{paddingRight:15}}
            onPress={()=>{props.navigation.navigate("LikedEventsScreen")}}
        >
            < Icon
                name = 'heart'
                color = 'red'
                size = {24}
            />
        </TouchableOpacity>
    )
}