import React from 'react';
import {StyleSheet, View, ScrollView} from 'react-native';
import {Layout, Text} from '@ui-kitten/components';

import Box from '../components/Box';


class LikedEventsScreen extends React.Component{
    
    renderEvents = ()=>{
        const { listEvents, likeDislikeEvent, getLikedStatusForEvent} = this.props.route.params;
        
        let arr = [];

        listEvents.map(
            (item, idx)=>{

                if(!getLikedStatusForEvent(item.id)){
                    return <View key={idx}/>
                }

                arr.push(
                    <Box 
                        key={idx}
                        navigation={this.props.navigation}
                        event={item}
                        likeDislikeEvent={likeDislikeEvent}
                        initialLikedStatus={getLikedStatusForEvent(item.id)}
                        getLikedStatusForEvent={getLikedStatusForEvent}
                    />
                )
            }
        )
        
        if(arr.length==0){
            return(
                <Text style={{alignSelf:'center', margin:16}}>No Liked Events</Text>
            )
        }

        return arr; 
    }
    
    render(){
        return(
            <Layout style={styles.container}>
                <ScrollView>
                {
                    this.renderEvents()
                }
                </ScrollView>
            </Layout>
        )
    }
}

export default LikedEventsScreen;

const styles = StyleSheet.create({
    container:{
        flex:1
    }
})