import React from 'react';
import { StyleSheet, Text, View,Image, ImageBackground } from 'react-native';
import WhiteSelectedCircle from '../components/WhiteSelectedCircle';
import WhiteCircle from '../components/WhiteCircle';
import Values from "../assets/Values";
import GestureRecognizer, {swipeDirections} from 'react-native-swipe-gestures';
class Page1 extends React.Component{
  //source={require('../assets/1.png')}
    render(){
        //console.log(this.props);
      return ( 
        <View style={styles.container}>
          <ImageBackground source={require('../assets/1.png')} style={{width:Values.Dim.W_Width, height:0.95*Values.Dim.W_Height, backfaceVisibility:'visible'}} resizeMode="stretch" >
          <View
            style={{height:0.89*Values.Dim.W_Height,width:Values.Dim.W_Width}}
           ></View>
          <View style={{flexDirection:'row',justifyContent:'center'}}>
              <WhiteSelectedCircle />
              <WhiteCircle />
              <WhiteCircle />
          </View>
         
          </ImageBackground>
             <View style={styles.whiteBand}>
                <Text style={{padding:15, fontSize:15 }}  onPress={()=>this.props.nextPress(4)} > Skip</Text>
                <Text style={{padding:15, fontSize:22 ,fontWeight:"600" }} onPress={()=>this.props.nextPress(2)}> Next</Text>
            </View>
        </View>
    );
    }
}

export default Page1;

var WindowHeight=Values.Dim.W_Height;
var WindowWidth=Values.Dim.W_Width;
const styles= StyleSheet.create({
    container:{
      flex:1,
      backgroundColor: Values.Colors.Grey,
      justifyContent:'flex-end',
      alignItems:'center'
    },
    whiteBand:{
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center',
        backgroundColor:'white',
        width: Values.Dim.W_Width,
        height:60,
    },
    button:
      {
        backgroundColor:Values.Colors.brandGreen,
        justifyContent:'center',
        alignItems:'center',
        height:0.065*WindowHeight,
        width:0.364*WindowWidth ,
        marginTop:0.058*WindowHeight,
        borderRadius:5
    }
  });
  
