import React from 'react';
import {View, Text, StyleSheet, ScrollView,TouchableOpacity, Linking} from 'react-native';
import Values from '../assets/Values';
import { Ionicons } from '@expo/vector-icons';
import {handleAndroidBackButton, removeAndroidBackButtonHandler} from './AndroidBackButton';
import LikedIcon from '../components/LikedIcon';

const googlePlayServicesLink = "https://policies.google.com/privacy";
const googleAnalyticsFirebaseLink = "https://firebase.google.com/policies/analytics";
const firebaseCrashAnalyticsLink = "https://firebase.google.com/support/privacy/";
const facebookLink = "https://www.facebook.com/about/privacy/update/printable";

const privacyPolicy = `
Follege Labs built the Follege app as an Ad Supported app. This SERVICE is provided by Follege Labs at no cost and is intended for use as is.

This page is used to inform visitors regarding our policies with the collection, use, and disclosure of Personal Information if anyone decided to use our Service.

If you choose to use our Service, then you agree to the collection and use of information in relation to this policy. The Personal Information that we collect is used for providing and improving the Service. We will not use or share your information with anyone except as described in this Privacy Policy.

The terms used in this Privacy Policy have the same meanings as in our Terms and Conditions, which is accessible at Follege unless otherwise defined in this Privacy Policy.
`;

const infoCollectionAndUse = `
For a better experience, while using our Service, we may require you to provide us with certain personally identifiable information, including but not limited to User Name, College Name, Phone Number. The information that we request will be retained by us and used as described in this privacy policy.

The app does use third party services that may collect information used to identify you.

Link to privacy policy of third party service providers used by the app
`;

const logData = `
We want to inform you that whenever you use our Service, in a case of an error in the app we collect data and information (through third party products) on your phone called Log Data. This Log Data may include information such as your device Internet Protocol (“IP”) address, device name, operating system version, the configuration of the app when utilizing our Service, the time and date of your use of the Service, and other statistics.
`;

const cookies = `
Cookies are files with a small amount of data that are commonly used as anonymous unique identifiers. These are sent to your browser from the websites that you visit and are stored on your device's internal memory.

This Service does not use these “cookies” explicitly. However, the app may use third party code and libraries that use “cookies” to collect information and improve their services. You have the option to either accept or refuse these cookies and know when a cookie is being sent to your device. If you choose to refuse our cookies, you may not be able to use some portions of this Service.
`;

const serviceProviders = `
We may employ third-party companies and individuals due to the following reasons:

To facilitate our Service;
To provide the Service on our behalf;
To perform Service-related services; or
To assist us in analyzing how our Service is used.
We want to inform users of this Service that these third parties have access to your Personal Information. The reason is to perform the tasks assigned to them on our behalf. However, they are obligated not to disclose or use the information for any other purpose.
`;

const security = `
We value your trust in providing us your Personal Information, thus we are striving to use commercially acceptable means of protecting it. But remember that no method of transmission over the internet, or method of electronic storage is 100% secure and reliable, and we cannot guarantee its absolute security.
`;

const linksToOtherSites = `
This Service may contain links to other sites. If you click on a third-party link, you will be directed to that site. Note that these external sites are not operated by us. Therefore, we strongly advise you to review the Privacy Policy of these websites. We have no control over and assume no responsibility for the content, privacy policies, or practices of any third-party sites or services.
`;

const childrensPrivacy = `
These Services do not address anyone under the age of 13. We do not knowingly collect personally identifiable information from children under 13. In the case we discover that a child under 13 has provided us with personal information, we immediately delete this from our servers. If you are a parent or guardian and you are aware that your child has provided us with personal information, please contact us so that we will be able to do necessary actions.
`;

const changesToThisPrivacyPolicy = `
We may update our Privacy Policy from time to time. Thus, you are advised to review this page periodically for any changes. We will notify you of any changes by posting the new Privacy Policy on this page.

This policy is effective as of 2020-08-20
`;

const contactUs = `
If you have any questions or suggestions about our Privacy Policy, do not hesitate to contact us at hello@follege.com.
`;




class PrivacyPolicy extends React.Component{
    componentDidMount(){
        handleAndroidBackButton(this.props.setFalse);
      }
        
      componentWillUnmount(){
        removeAndroidBackButtonHandler();
      }
      openLink = (url)=>{
        Linking.openURL(url).catch(err => alert("Couldn't load page :/"))
    }
  
    render(){
        return(<View style={{flex:1}}>
              <View style={styles.header}>
            <TouchableOpacity
                style={styles.button}
                onPress={this.props.setFalse}
                >
            <View style={{height:30, width:30, borderRadius:30}}>
                 <Ionicons 
                        name="md-arrow-round-back"  
                         color="white" size={0.080*Values.Dim.W_Width} />
                </View>
            </TouchableOpacity>

              
                
            </View>
            <ScrollView>
                <View style={styles.container}>
                    <Text style={styles.h1}>Privacy Policy</Text><Text>{privacyPolicy}</Text>
                    <Text style={styles.h1}>Information Collection and Use</Text><Text>{infoCollectionAndUse}</Text>
                    <Text style={styles.txtLink} onPress={()=>{this.openLink(googlePlayServicesLink)}}>Google Play Services</Text>
                    <Text style={styles.txtLink} onPress={()=>{this.openLink(googleAnalyticsFirebaseLink)}}>Google Analytics for Firebase</Text>
                    <Text style={styles.txtLink} onPress={()=>{this.openLink(firebaseCrashAnalyticsLink)}}>Firebase Crashlytics</Text>
                    <Text style={styles.txtLink} onPress={()=>{this.openLink(facebookLink)}}>Facebook</Text>
                    <Text/>
                    <Text style={styles.h1}>Log Data</Text><Text>{logData}</Text>
                    <Text style={styles.h1}>Cookies</Text><Text>{cookies}</Text>
                    <Text style={styles.h1}>Service Providers</Text><Text>{serviceProviders}</Text>
                    <Text style={styles.h1}>Security</Text><Text>{security}</Text>
                    <Text style={styles.h1}>Link to Other Sites</Text><Text>{linksToOtherSites}</Text>
                    <Text style={styles.h1}>Children's Privacy</Text><Text>{childrensPrivacy}</Text>
                    <Text style={styles.h1}>Changes to this Privacy Policy</Text><Text>{changesToThisPrivacyPolicy}</Text>
                    <Text style={styles.h1}>Contact Us</Text><Text>{contactUs}</Text>
                </View>
            </ScrollView>
            </View>
        )
    }
}

export default PrivacyPolicy;


const styles = StyleSheet.create({
    container:{
        flex:1,
        margin:16
    },
    header:{
        flexDirection:'row',
        width:Values.Dim.W_Width,
        height:0.11*Values.Dim.W_Height,
        backgroundColor:Values.Colors.Grey,
        justifyContent:'flex-start',
         alignItems:'flex-end',
         paddingBottom:10,
         paddingLeft:10
    },
    headerButtonContainer:{
        paddingTop:15,
        justifyContent:'center',
        alignItems:'center'
    },
    h1:{
        fontWeight:'bold'
    },
    txtLink:{
        color:'blue'
    }
})