import React from 'react';
import { StyleSheet, Text, View ,ImageBackground, TouchableOpacity} from 'react-native';
import WhiteSelectedCircle from '../components/WhiteSelectedCircle';
import WhiteCircle from '../components/WhiteCircle';
import Values from "../assets/Values";
class Page3 extends React.Component{
  nextPress(){
    
  }
    render(){
      return ( 
        <View style={styles.container}>
          <ImageBackground source={require('../assets/3.png')} style={{width:Values.Dim.W_Width, height:0.95*Values.Dim.W_Height, backfaceVisibility:'visible'}} resizeMode="stretch" >
        <View
          style={{height:0.89*Values.Dim.W_Height,width:Values.Dim.W_Width}}
         ></View>
        <View style={{flexDirection:'row',justifyContent:'center'}}>
            <WhiteCircle />
            <WhiteCircle />
            <WhiteSelectedCircle />
        </View>
       
        </ImageBackground>
         

             <TouchableOpacity style={styles.whiteBand} onPress={()=>this.props.nextPress(4)}>
                
                <Text style={styles.textStyle} > Continue</Text>
            </TouchableOpacity>
        </View>
    );
    }
}

export default Page3;

var WindowHeight=Values.Dim.W_Height;
var WindowWidth=Values.Dim.W_Width;
const styles= StyleSheet.create({
    container:{
      flex:1,
      backgroundColor: Values.Colors.Grey,
      justifyContent:'flex-end',
      alignItems:'center'
    },
    whiteBand:{
        flexDirection:'row',
        justifyContent:'center',
        alignItems:'center',
        backgroundColor:Values.Colors.brandGreen,
        width: Values.Dim.W_Width,
        height:60,
    },
    textStyle:{
      color:'white',
      fontWeight:'800',
      fontSize:25,
      
    }
  });
  
