import React from 'react';
import {View, Text, TouchableOpacity, StyleSheet, BackHandler} from 'react-native';
import {Feather as Icon} from '@expo/vector-icons';

import {colors} from '../constants/colors';

class Tag extends React.Component{


    select = ()=>{
        this.props.addRemoveInterest(this.props.tag.id);
    }

    render(){

        var {col, icon, interest} = this.props.tag;
        
        if(!this.props.isSelected){
            col = colors.btnDull;
        }

        const fontSize = 14 ;
        const fontBoldness = "bold";
        const iconSize = 20 ;
        const borderWidth = 0;
        const borderColor = "white";

        return(
            <TouchableOpacity
                style={{...styles.container, backgroundColor:col, borderWidth:borderWidth, borderColor:borderColor}}
                onPress = {this.select}
            >
                <Icon name={icon} size={iconSize} color="white"/>
                <Text style={{...styles.interest, fontSize:fontSize, fontWeight:fontBoldness }}>{interest}</Text>
            </TouchableOpacity>
        )
    }
}

export default Tag;

const styles = StyleSheet.create({
    container:{
        flexDirection:'row', 
        alignItems:'center',
        minHeight: 24,
        padding: 10,
        borderRadius:2,
    },
    interest:{
        paddingLeft:8,
        color:'white',
    }
})