import React from 'react';
import {View, StyleSheet, Linking, Alert, TouchableOpacity } from 'react-native';
import { WebView } from 'react-native-webview';
import { Layout, Text } from '@ui-kitten/components';
 
import LikedIcon from '../components/LikedIcon';

import {Ionicons} from '@expo/vector-icons';

class InAppBrowserEvent extends React.Component{

    componentDidMount(){
        this.props.navigation.setOptions({
            headerRight : ()=>(
                <TouchableOpacity
                    onPress={()=>{Linking.openURL(this.props.route.params.link).catch(err => alert("Couldn't load page :/"))}}
                    style={{marginRight:16}}
                >
                    <Ionicons name="md-open" size={24} color="white"/>
                </TouchableOpacity>
            )
        })
    }

    render(){
    //alert(this.props.route.params.link);
        const url = this.props.route.params.link;

        return(
            <>
                <View style={{height:'100%', width:'100%'}}>
                    <WebView source={{uri:url}}/>
                </View>
            </>
        )
    }



}

export default InAppBrowserEvent;


const styles = StyleSheet.create({
    container:{

    }
})

  