import React from 'react';
import { StyleSheet, Text, View,TouchableOpacity,TextInput ,KeyboardAvoidingView,TouchableWithoutFeedback,Keyboard , Image} from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import Values from "../assets/Values";
import {handleAndroidBackButton, removeAndroidBackButtonHandler} from './AndroidBackButton';
class Mobile extends React.Component{
    constructor(props) {
        super(props);
        this.state = {mobNo: "" };
      }
      componentDidMount(){
        handleAndroidBackButton(()=>{this.props.nextPress(5)});
      }
        
      componentWillUnmount(){
        removeAndroidBackButtonHandler();
      }
    updateMobile()
    {
        if(this.state.mobNo.length!=10) 
        {
            alert("Enter a 10 digit Mobile Number")
        }
        else this.props.updateMobile(this.state.mobNo)  // will update mobile number in intro.js and change screen also
    }
    render(){
      //console.log(Values.Dim.W_Height)
      // console.log(Values.Dim.W_Width)
      return ( 
        <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}  style={styles.container}>
          <KeyboardAvoidingView
            behavior="position"
            style={styles.container}
           >
              <TouchableOpacity style={styles.icons} onPress={()=>{this.props.nextPress(5)}} ><Ionicons name="md-arrow-round-back"  color="white" size={0.080*WindowWidth}/></TouchableOpacity>
              <View style={styles.upperBox}>
               < Image style={styles.image}source={require('../assets/phonefinal.png')} />
              </View>
              <View style={{ justifyContent:'center',alignItems:'center' ,margin:30}} ><Text style={{color:'white', fontSize:18, fontFamily:'Montserrat'}}>Enter your Mobile Number</Text></View>
              <View 
                style={styles.input} >
              <TextInput
                    style={{ height: 0.061*WindowHeight, width: 0.851*WindowWidth,
                          backgroundColor:'white',
                          borderRadius:4 ,paddingLeft:0.024*WindowWidth  
                            }}
                           
                    keyboardType="numeric"
                    placeholder="99999 99999"
                      placeholderTextColor= 'grey'
                      onChangeText={  text => this.setState({ 
                                                mobNo: text
                                             })
                                    }
                        value={this.state.mobNo}
                        />
             </View>
            <View style={styles.buttonCol}>
                <TouchableOpacity style={styles.button1}  onPress={()=>this.updateMobile()}><Text style={styles.whiteText}>NEXT</Text></TouchableOpacity>
                <TouchableOpacity style={styles.button2}  onPress={()=>this.props.nextPress(7)}><Text style={{ color:'white',
       fontSize:20}}>SKIP</Text></TouchableOpacity>
            </View>
            
            
            </KeyboardAvoidingView>
            </TouchableWithoutFeedback>
    );
    }
}

export default Mobile;

var WindowHeight=Values.Dim.W_Height;
var WindowWidth=Values.Dim.W_Width;
const styles= StyleSheet.create({
    container:{
      flex:1,
      backgroundColor: Values.Colors.Grey,
      justifyContent:'flex-start',
      alignItems:'center'
    },
    whiteBand:{
        flexDirection:'row',
        justifyContent:'center',
        alignItems:'center',
        backgroundColor:'white',
        width: 0.9*Values.Dim.W_Width,
        height:50,
        margin:10
    },
    upperBox:{
        flexDirection:'row',
        justifyContent:'center',
        alignItems:'center',
        backgroundColor:Values.Colors.Grey,
        marginTop:0.0625*Values.Dim.W_Height,
        height:0.28*Values.Dim.W_Height,
        width:0.9*Values.Dim.W_Width
    },
    buttonCol:{
        flexDirection:'column',
        justifyContent:'center',
        alignItems:'center',
        
        
    },
    input:{
        marginTop:5,
        marginLeft:0.029*Values.Dim.W_Width
    },
    button1:
      {
        backgroundColor:Values.Colors.brandGreen,
        justifyContent:'center',
        alignItems:'center',
        width: 0.35*Values.Dim.W_Width,
        height:45,
        marginTop:0.058*WindowHeight,
        borderRadius:4
       
    },
    button2:
      {
        backgroundColor:Values.Colors.Grey,
        justifyContent:'center',
        alignItems:'center',
        width: 0.45*Values.Dim.W_Width,
        height:50,
        marginTop:0.028*WindowHeight,
       
    },
    whiteText:{
       color:'white',
       fontSize:22,
       fontFamily:'open-sans'
    },
    emptyBox:
      {
        flexDirection:'column',
        justifyContent:'center',
        alignItems:'center',
        width: Values.Dim.W_Width,
        height:150,
        marginTop:0.058*WindowHeight,
        padding:20
    },
    icons:{
      marginTop:0.045*WindowHeight,
      marginLeft:0.024*WindowWidth,
      marginBottom:5
    },
    image:{
      // height:450,
      // width:450,
      maxHeight:'80%',
      // maxWidth:'44%',
      resizeMode:'contain'
    }
});
  
