import * as React from 'react';
import { createStackNavigator } from '@react-navigation/stack';

import SearchScreen from '../MainApp/SearchScreen';
import SearchResult from '../MainApp/SearchResult';
import Title from './Title';
import LikedIcon from './LikedIcon';
import { colors } from '../constants/colors';


const Stack = createStackNavigator();

function SearchStack(props) {

  //console.log("in SearchStack")
  //console.log(props.route.params.setHeaderVisibility)


  return (
      <Stack.Navigator
        screenOptions={{
            headerTitleAlign: 'center',
            headerTintColor:'white',
            headerBackTitle:"Back",

            headerStyle : {backgroundColor: colors.primary},
            headerTitle : () => (<Title/>) ,
        }}
      >
        <Stack.Screen 
          name="SearchScreen" 
          component={SearchScreen}
          initialParams={{...props.route.params}}
        />
        <Stack.Screen
          name="SearchResult"
          component={SearchResult}
          initialParams={{getLikedStatusForEvent : props.route.params.getLikedStatusForEvent, likeDislikeEvent : props.route.params.likeDislikeEvent}}
        />
      </Stack.Navigator>
  );

  
}

export default SearchStack;