import React from 'react';
import { StyleSheet, View } from 'react-native';
import Values from "../assets/Values";

function WhiteSelectedCircle ()
{
    return (  
     <View style={styles.circle} >
        <View style={styles.blackCircle}>
            <View style={styles.smallcircle}></View>
        </View>
    </View> );
}

      
  
  
export default WhiteSelectedCircle;

const styles= StyleSheet.create({
    
    circle:{
        width: 20,
        height:20,
        justifyContent:'center',
        alignItems:'center',
        borderRadius:20,
        backgroundColor:'white',
        margin:10
    },
    blackCircle:{
        width: 17,
        height:17,
        justifyContent:'center',
        alignItems:'center',
        borderRadius:20,
        backgroundColor:Values.Colors.Grey,
    },
    smallcircle:{
        width: 12,
        height:12,
        borderRadius:20,
        backgroundColor:'white',
    }
  });
  
